import assertNever from "assert-never";
import { EnhetsregisterActions } from "./actions";
import { Appstate, initialState } from "./domain";

export function reducer(state: Appstate, action: EnhetsregisterActions): Appstate {
    switch (action.type) {
        case "DATA/HENTER_DATA":
            return {
                ...state,
                loading: true
            };
        case "DATA/SUBMITTED_FORM":
            return {
                ...state,
                submitted: true,
                loading: true,
                error: ""
            };
        case "DATA/HENTET_DATA_OK": {
            return {
                ...state,
                data: action.data,
                submitted: true,
                loading: false,
                error: ""
            };
        }
        case "DATA/HENTING_AV_DATA_ERROR": {
            return {
                ...state,
                loading: false,
                error: action.error,
                submitted: true
            };
        }
        case "SOK/RESET": {
            return initialState;
        }
        default: {
            assertNever(action);
            return state;
        }
    }
}
